
.CircleComponent{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    outline: solid 2px rgb(26, 26, 26);
    margin: 50px 150px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}
  
.main-box{
    width: 90%;
    margin: auto;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    gap: 5rem;
}
  
.box{
    text-align: center;
}
  .empty-box{
    width: 300px;
    /* height: 350px; */
    padding: 50px 10px;
    border: 1px solid black;
  }
  
  .circle {
    width: 80px;
    height: 80px;
    border: 1px solid black;
    border-radius: 50%;
    margin: 10px;
  }
  
  .input-box{
    padding: 20px 100px;
    font-size: 18px;
  }
  
  .btn{
    padding: 20px 105px;
    margin-top: 20px;
    font-size: 20px;
  }
  
  .btn:hover{
    background: turquoise;
  }

  